import './styles/style.css';
import '@fortawesome/fontawesome-free/css/all.css';

import twitter from './img/twitter.svg';
import reload from './img/rotate.svg';

const twit = document.querySelector('#im');
twit.src = twitter;

const reloadImage = document.querySelector('#reload');
reloadImage.src = reload;

const toDoList = document.querySelector('#to-do-list');

const listTask = [
  {
    index: 0,
    description: 'desc-1',
    completed: true,
  },
  {
    index: 1,
    description: 'desc-2',
    completed: false,
  },
  {
    index: 2,
    description: 'desc-3',
    completed: true,
  },
];

function renderToDoList() {
  listTask.forEach((task) => {
    const listItem = document.createElement('li');
    listItem.textContent = `${listTask.description} - ${listTask.completed ? 'Completed' : 'Not Completed'}`;
    toDoList.appendChild(listItem);
  });
}

window.addEventListener('load', renderToDoList);